.navigation-bar-left {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: black;
}

.nb-left-left {
    font-family: "Anton", sans-serif;
    text-align: center;
    padding-right: 25px;
    border-right: 3px solid black;
}

.nb-trix-name {
    font-size: 2.5vw;
    text-shadow: #962d2f 0 4px;
    padding: 0;
}

.nb-conference-2024 {
    font-size: 1.2vw;
}

.nb-left-right {
    padding-left: 25px;
}

.nb-date {
    font-size: 1.3vw;
    font-weight: bold;
}

.nb-location {
    font-size: 1vw;
    font-weight: bolder;
}