.navigation-bar-right {
    display: flex;
    flex-direction: column;
}

.nb-instagram {
    font-size: 1vw;
    margin-bottom: 0.5vw;
    border: 2px solid black;
    border-radius: 5px;
    padding: 2px 10px;
    transition: color, background-color 0.7s ease;
}

.nb-instagram:last-child {
    margin-bottom: unset;
}

.nb-instagram:hover {
    background-color: black;
    color: #dddddd;
}