.navigation-bar-center {
    display: flex;
    flex-direction: row;
    margin-right: auto;
    margin-left: 10vw;
}

.navigation-bar-center > div {
    margin-right: 1.5vw;
    font-size: 1.35vw;
    transition: font-weight 0.2s ease;
}

.navigation-bar-center > div > a {
    color: #333333;
}

.navigation-bar-center > div:hover {
    font-weight: bolder;
    text-decoration: underline;
    color: black;
}